import { BuildContext, Button, Component, ComponentState, Container, getServerActionsClient, Heading, Row, StatefulComponent, TextInput } from "@equantic/runtime";

export class TodoList extends StatefulComponent {
    createState() {
        return new TodoListState(this)
    }

    async getTodos() {
        return await getServerActionsClient().invoke('TodoList/GetTodos', [])
    }

    async addTodo(title: string) {
        return await getServerActionsClient().invoke('TodoList/AddTodo', [title])
    }

    async toggleTodo(id: string) {
        return await getServerActionsClient().invoke('TodoList/ToggleTodo', [id])
    }

    async deleteTodo(id: string) {
        return await getServerActionsClient().invoke('TodoList/DeleteTodo', [id])
    }

}


export class TodoListState extends ComponentState {
    _component: TodoList;
    _needsRender: boolean = false;
    _todos: Todo[] = [];
    _newTodoTitle: string = "";
    constructor(component: TodoList) {
        super();
        this._component = component;
    }

    setState(fn: () => void) {
        fn();
        this._needsRender = true;
        this._component._scheduleRender();
    }

    onInit(): void {this._ = this.loadTodos();}

    async loadTodos(): Promise<void> {this._todos = await Component.GetTodos();this.setState(() => {});}

    async handleAdd(): Promise<void> {if (string.IsNullOrWhiteSpace(this._newTodoTitle)) return ;await Component.AddTodo(this._newTodoTitle);this._newTodoTitle = '';await this.loadTodos();}

    async handleToggle(id: string): Promise<void> {await Component.ToggleTodo(id);await this.loadTodos();}

    async handleDelete(id: string): Promise<void> {await Component.DeleteTodo(id);await this.loadTodos();}

    build(context: BuildContext) {
        return (
        new Container({

            className: 'todo-container',
            children: [

                new Heading({

                    content: 'eQuantic Todo List',
                }
                )
                ,
                new Row({

                    gap: '10px',
                    children: [

                        new TextInput({

                            value: this._newTodoTitle,
                            placeholder: 'What needs to be done?',
                            onChange: (val) => this.setState(() => this._newTodoTitle = val),
                        }
                        )
                        ,
                        new Button({

                            text: 'Add',
                            className: 'btn btn-primary',
                            onClick: () => {this.handleAdd();},
                        }
                        )
                        ,
                    ]

                }
                )
                ,
            ]

        }
        )
        );
    }

}


